.home-banner{
    background-image: url(/lexoj.al/static/media/background.1ab6aa69.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    text-align: center;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.banner-title{
    font-weight: bold;
    font-size: 48px;
}
.banner-action{
    background-color: white;
    color: black;
    font-size: 20px;
    border-radius: 5px;
    padding: 10px 10px;
    cursor: pointer;
}
.banner-action:hover{
    background-color: rgb(190, 190, 190);
}

.mobileMenu{
    width: 80%;
    text-align: left;
    display: none;
}
.mobileMenu ion-icon{
    font-size: 24px;
}

@media screen and (max-width: 800px){
    .mobileMenu{
        display: flex;
    }
}

@media screen and (max-width: 600px) {
    .home-banner{
        justify-content: space-around;
    }
}
.menuContainer{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    overflow: none;
    z-index: 3;
    background-color: #0F1111;
}
.menuContainer ion-icon{
    position: relative;
    top: 10px;
    left: 93%;
    color: white;
    font-size: 40px;
}
.menuLogBar{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.menuLogBar p{
    border: 1px solid rgb(255, 255, 255);
    text-align: center;
    font-size: 24px;
    border-radius: 5px;
    padding: 10px 0;
    margin: 0 0 20px 0;
    cursor: pointer;
    color: white;
}
.menuLogBar .links{
    text-decoration: none;
    width: 50%;
}
.menuLogBar img{
    width: 200px;
    height: 200px;
    border-radius: 100px;
    margin-bottom: 20px;
}
.menuLogBar span{
    font-size: 24px;
    margin-bottom: 20px;
}
@media screen and (max-width: 600px) {
    .menuContainer ion-icon{
        left: 85%;
    }
    .menuLogBar .links{
        width: 80%;
    }
}
.books-Section{
    border-top: 7px solid rgb(100, 100, 100);
    background-color: #0F1111;
    padding: 10px 0;
}
.section-h{
    font-family: 'Merriweather', serif;
    font-size: 28px;
    padding: 10px 0px 0px 0px;
    color: white;
    text-align: center;
    margin: 0;
}
.section-p{
    font-family: 'PT Serif', serif;
    font-size: 24px;
    color: rgb(150, 150, 150);
    text-align: center;
    margin: 0;
}
.row{
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.row div{
    width: 35%;
}
.row img{
    width: 35%;
    height: auto;
    min-width: 400px;
}
.row p{
    text-align: start;
}
.books-container-home{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 0fr;
    padding: 10px 20px;
}

@media screen and (max-width: 1000px) {
    .books-container-home{
