.book-card{
    margin: 10px 10px;
    width: 175px;
    height: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    transition: .5s;
}
.book-card:hover{
    transform: rotate(5deg);
}
.book-card img{
    width: 100%;
    height: 75%;
}
.card-details{
    width: 100%;
    height: 25%;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.card-title{
    margin: 0;
    width: 100%;
    height: 60%;
    overflow: hidden;
    font-weight: bold;
    color: rgb(230, 230, 230);
    letter-spacing: 1px;
}
.card-subdetails{
    width: 100%;
    height: 40%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: rgb(200, 200, 200);
}
.card-author{
    overflow: hidden;
    margin: 0;
    width: 70%;
    height: 100%;
}
.card-views{
    margin: 0;
    width: 30%;
    height: 100%;
    text-align: end;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: #33adff;
}
.card-views p{
    margin: 0;
    width: 50%;
    height: 100%;
}
.card-icon{
    padding-top: 2px;
}

@media screen and (max-width: 600px){
    .book-card{
        margin: 10px 0;
        width: 150px;
    }
    .card-author{
        font-size: 14px;
    }
}
.pagination{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 20px 0;
}
.pagination p{
    color: white;
    margin: 0 20px;
    font-size: 20px;
}
.arrow{
    cursor: pointer;
    padding-top: 5px;
}
.linearMap{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    color: gray;
    font-size: 16px;
    padding-left: 20px;
}
.linearMap p{
    margin-left: 15px;
}
.books-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 0fr;
    padding: 0 20px;
}
.browse-screen{
    background-color: #0F1111;
    padding-top: 70px;
}
.categories-container{
    color: rgb(170, 170, 170);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 0fr;
    padding: 10px 20px;
    line-height: 5px;
    box-shadow: inset 0 0 10px #000000;
}
.category{
    cursor: pointer;
    font-size: 18px;
}
.category:hover, .categories-triggers p:hover{
    color:  white;
}
.categories-triggers{
    padding: 10px 20px 0 20px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    color:  rgb(170, 170, 170);
    font-size: 20px;
}
.categories-triggers p{
    cursor: pointer;
}

.bookCardHolder{
    width: 175px;
    height: 300px;
    margin: 10px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.detailsHolder{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 25%;
}
.imgHolder{
    width: 100%;
    height: 75%;
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
}
.titleHolder{
    background-color: #2b2b2b;
    width: 100%;
    height: 30%;
}
.subdetailsHolder{
    width: 100%;
    height: 30%;
    display: flex;
    justify-content: space-between;
}
.authorHolder{
    width: 50%;
    height: 100%;
    background-color: #2b2b2b;
}
.soldHolder{
    width: 30%;
    height: 100%;
    background-color: #2b2b2b;
}
@-webkit-keyframes pulse {
	0% {
		background-color: #2b2b2b;
    }
    
    33% {
		background-color: #303030;
	}

	66% {
		background-color: #333333;
	}

	100% {
		background-color: #3a3a3a;
	}
}
@keyframes pulse {
	0% {
		background-color: #2b2b2b;
    }
    
    33% {
		background-color: #303030;
	}

	66% {
		background-color: #333333;
	}

	100% {
		background-color: #3a3a3a;
	}
}

@media screen and (max-width: 1000px) {
   .books-container{
        grid-template-columns: 1fr 1fr 0fr;
   }
   .categories-container{
        grid-template-columns: 1fr 1fr 1fr 0fr;
   }
}

@media screen and (max-width: 600px) {
    .books-container{
        grid-template-columns: 1fr 0fr;
   }
   .categories-container{
        grid-template-columns: 1fr 1fr 0fr;
    }
    .bookCardHolder{
        width: 150px;
    }
}
.book-screen{
    background-color: #0F1111;
    padding-top: 70px;
}
.book-info{
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.book-cover{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.book-cover img{
    width: 50%;
    height: auto;
}
.book-details{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}
.book-title{
    margin: 0;
    font-family: 'Roboto Slab', serif;
    color: white;
    font-size: 30px;
}
.book-details div .book-detail{
    display: grid;
    grid-template-columns: 1fr 4fr;
    font-family: 'Roboto', sans-serif;
