    font-size: 18px;
    letter-spacing: 1px;
    cursor: pointer;
}
.book-description{
    font-family: 'Roboto', sans-serif;
    color: rgb(201, 201, 201);
    font-size: 17px;
    line-height: 20px;
}

.book-cover p{
    border: 1px solid white;
    border-radius: 5px;
    color: white;
    width: 50%;
    text-align: center;
    font-size: 24px;
    padding: 5px 0;
    align-items: center;
    cursor: pointer;
}
.book-cover p:hover{
    background-color: white;
    color: black;
}

.bookPageHolder{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.bookPageCoverHolder{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 50%;
}
.bookPageDetailsHolder{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.bookPageImageHolder{
    margin: 20px 0;
    width: 45%;
    height: 400px;
    background-color: #2b2b2b;
}
.bookPageActionHolder{
    margin: 20px 0;
    width: 45%;
    height: 50px;
    background-color: #2b2b2b;
}
.bookPageSubDetailsHolder{
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.bookPageSubDetailHolder, .bookPageTitleHolder{
    margin: 20px 0;
    width: 60%;
    height: 40px;
    background-color: #2b2b2b;
}
.bookPageDescriptionHolder{
    margin: 20px 0;
    width: 80%;
    height: 150px;
    background-color: #2b2b2b;
}

@media screen and (max-width: 800px) {
    .book-info{
        flex-direction: column;
    }
    .book-cover{
        width: 100%;
    }
    .book-details{
        width: 100%;
    }
    .book-details div .book-detail{
        grid-template-columns: 1fr 2fr;
    }
    .bookPageHolder{
        flex-direction: column;
    }
    .bookPageCoverHolder, .bookPageDetailsHolder{
        width: 100%;
    }
}
@media screen and (max-width: 600px) {
    .book-cover p{
        width: 80%;
    }
    .book-cover img{
        width: 80%;
    }
    .bookPageImageHolder, .bookPageActionHolder{
        width: 80%;
    }
    .bookPageSubDetailHolder, .bookPageTitleHolder{
        width: 70%;
    }
}
